@import 'colors.scss';

.modal-container {
  position: absolute;
  z-index: 9999;
  width: 201px;
  height: 300px;
  box-sizing: border-box;
  box-shadow: 0px 3px 18px #00000029;
  border: 1px solid #43425D;
  border-radius: 10px;
  background: $white;
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 25px;
}

.close {
  position: absolute;
  top: 6px;
  right: 7px;
  cursor: pointer;

  &:focus {
    outline: none;
  }

  svg {
    color: $alto;
  }
}

.input-group {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  color: $alto;
  font-size: 9px;

  input {
    border-bottom: 1px solid $alto;
    width: 100%;
  }

  .icon-container {
    border-bottom: 1px solid $alto;
  }

  .fa-calendar-alt {
    width: 18px;
    height: 20px;
  }

  .fa-clock {
    width: 20px;
    height: 20px;
  }

  .error-input {
    border-bottom: 1px solid $bittersweet;
  }
}

.input-container {
  display: flex;
  flex-direction: column;
  position: relative;
}

.button-group {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;

  button {
    cursor: pointer;

    &:focus {
      outline: none;
    }

    &:disabled {
      cursor: not-allowed;
    }
  }

  button:first-child {
    color: $bittersweet;
  }

  button:last-child {
    color: $kimberly;
  }
}

.text-error {
  position: absolute;
  bottom: -12px;
  left: 0;
  color: $bittersweet;
}
